html, body, #root {
    height: 100%;
    width: 100%;
    min-width: 1200px;
    margin: 0;
    padding: 0;
}

.application {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.page-main {
    flex-grow: 1;
}

.product-container__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.product-container .product-filter {
    margin: 0;
    padding: 0;
    width: 200px;
    flex: 0 0 auto;
}

.product-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 0 1 auto;
    justify-content: left;
    width: 100%;
}

.card {
    width: 280px;
    margin-bottom: 30px;
    padding: 10px;
    position: relative;
}
.card .product-name {
    margin: 0;
    padding: 0;
    min-height: 54px;
}

.card .product-picture {
    object-fit: contain;
    width: 280px;
    height: 250px;
    margin-left: auto;
    margin-right: auto;
}

.card .description {
    padding: 0;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    position: relative;
    text-overflow: ellipsis;
}

