.card .favorite {
    position: absolute;
    width: 20px;
    height: 20px;
    right: 20px;
    top: 70px;
    font-size: 0;
    transform: rotate(45deg);
    border: 2px solid red;
    border-left-color: transparent;
    border-top-color: transparent;
}

.card .favorite::before, .card .favorite::after {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    border-radius: 50%;
    border: 2px solid red;
    border-right-color: transparent;
    border-bottom-color: transparent;
}

.card .favorite::before {
    border: 2px solid red;
    border-right-color: transparent;
    border-bottom-color: transparent;
    transform: translate(-12px, -2px) rotate(-45deg);
}

.card .favorite::after {
    transform: translate(-2px, -12px) rotate(45deg);
}

.card .favorite.is-favorite {
    background-color: red;
}

.card .favorite.is-favorite::after, .card .favorite.is-favorite::before {
    background: linear-gradient(to right bottom, red 0, red 50%, transparent 50%, transparent 100%);
}
